import {
  fetchNewsPending,
  fetchNewsSuccess,
  fetchNewsError
} from '../actions';

function fetchNews() {
  return async dispatch => {
    dispatch(fetchNewsPending());
    await fetch('https://swapi.dev/api/people')
      .then(res => res.json())
      .then(res => {
        if (res.error) {
          throw(res.error);
        }
        // console.log(res.results)
        dispatch(fetchNewsSuccess(res.results))
        return res;
      })
      .catch(error => {
        dispatch(fetchNewsError(error));
      })
  }
}

export default fetchNews;
